import React, { LegacyRef, MutableRefObject, ReactChild } from 'react';
import Box from '@mui/material/Box';
import { Container, Grid, Button } from '@mui/material';
import NavigateNextIcon from '@mui/icons-material/NavigateNext';
import TextForm, { P_TextForm } from './TextForm';
import { Link } from 'react-router-dom';
import { SCREEN_NAMES } from './const';

export default function ScreenHome(props: { propsTextForm: P_TextForm }) {
	return (
		<Container>
			<Grid container spacing={3}>
				<Grid item md={8} sm={12} zeroMinWidth>
					<TextForm
						value={props.propsTextForm.value}
						onChange={props.propsTextForm.onChange}
					/>
				</Grid>
				<Grid item md={4} sm={12} zeroMinWidth>
					<Button
						variant='contained'
						endIcon={<NavigateNextIcon />}
						//sx={{ justifyContent: 'flex-start' }}
						component={Link}
						to={SCREEN_NAMES.edit}
					>
						 / 
					</Button>
				</Grid>
			</Grid>
		</Container>
	);
}
